import React from 'react';
import UrlInput from './UrlInput';
import DataElement from './DataElement';

class Scoresheet extends React.Component {
  render() {
    const {comparison, comparisonIndex, datavalue} = this.props;
    if (comparison != undefined) {

      if (datavalue && comparison.length >= 1) {
        if (comparison.indexOf(datavalue) != -1) {
          return (<span className="dataElement scores">O</span>)
        } else {
          return (<span className="dataElement scores"></span>)
        }
      } else {
        console.log(comparison+" "+comparisonIndex);
        if (comparison == comparisonIndex) {
          return (<span className="dataElement scores">&#x2191;</span>)
        } else {
          return (<span className="dataElement scores">&#x2193;</span>)
        }
      }
    } else {
      return (<span className="dataElement scores"></span>)
    }
  }
}

export default Scoresheet;



